import { Component, OnInit } from "@angular/core";
@Component({
  selector: "app-lp-geral",
  templateUrl: "./lp-geral.component.html",
  styleUrls: [],
})
export class LpGeralComponent implements OnInit {


  constructor() {}

  ngOnInit(): void {
    this.doRedirect();
  }

  doRedirect(): void {
    // check if url contains query params to preserv in redirect
    let lpPath: string = 'https://lp.movidazerokm.com.br/carro-por-assinatura';
    let processUrl = window.location.href.split('?');
    let redirectPath = `${lpPath}`;
    if (processUrl.length > 1) {
      redirectPath = `${lpPath}?${processUrl[1]}`;
    }
    window.location.href = redirectPath;
  }
}
